body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a{
  color: black;
}

.brand-logo,h2{

  font-family: 'Grand Hotel', cursive;
  
}

.auth-card{
  padding: 2.5%;
  text-align: center;
  max-width: 550px;
  border-radius: 30px;
  background: #E1DDD0;
  box-shadow:  20px 20px 60px #bfbcb1,
             -20px -20px 60px #fffeef;
}

.gallery{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.item{
  width: 30%;
}

.home-card{
  max-width: 500px;
  height: -webkit-max-content;
  height: max-content;
  margin: 26px auto;
  
}
.collection-item{
  width: 100%;
}


#toast-container{
  top:30px !important;
  right: 20px !important;
  left: auto !important;
}


.material-icons:hover{
  cursor: pointer;
}

/* Login-page */
.mycard{
    margin: 5% 10%;
    align-items: center;
    float: right;
    width: 40%;
}

.login-image{
  width: 35%;
  height: 35%;
  float: left;
  margin-left: 2%;
}

.links-text{
  text-decoration: none;
}


/* Signup page */
.signup-image{
  width: 25%;
    height: 25%;
    float: right;
    margin-top: 3%;
    margin-right: 13%;

}

@media (max-width: 600px) {

  .login-image{
      width: 90%;
      height: 90%;
      float: none;
  }

  .mycard{
      align-items: center;
      margin: 2% 2% 6% 2%;
      width: 95%;
      float: none;
  }

  .editContact-form{
    width: 95%;
    margin: 2%;
  }

}

.resetPasswordCard{
  width: 300px;
  padding: 3%;
}

