 .navbar-title{
    color: white;
    margin:"0px 0px 0px 67.547px";
    font-size: 20px;
}

.nav-link-text:hover{
    color: #FEC947;
}
  
.dropdown-text:hover{
    color: white;
}

.dropdown {
    position: relative;
    display: inline-block;
}
  
.dropdown-menu {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}
  

.dropdown-menu a {
    color: white;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}
  
.dropdown-menu a:hover {
    background-color: #E0F2FC;
}
  
  
.dropdown:hover .dropdown-menu {
    display: block;
}


.navbar-toggler{
    background-color: #E0F2FC !important;
}


